import React from 'react'
import './about.css'

export default function About() {
  return (
    <div className='About'>
      <div className='containerContent'>
        <div className="Texts">
          <h2 className="titleAbout">Le Thanh Dat</h2>
          <div className="mainContent">
            <p>Hello there!</p>
            <p>My name is Dat and I’m just starting first year of my Software Engineering studies at VietNam - Korea University of Information and Communication Technology in Da Nang, Viet Nam.
You can view my Github profile <a className="githubProfile" href="https://github.com/Wortzzzzz">https://github.com/Wortzzzzz.</a></p>
            <p>I am very passionate about building stuff that other people can use.</p>
            <p>I love all techs, but my favorite ones are React and Node.js.</p>
            <p>I would like to contribute to open source. I enjoy challenges that enable me to grow and find application in what is taught to me and use Pomodoro technique to learn as quickly as possible.
I love connecting with people and know about their community and have a keen interest in there past projects and endeavors.</p>
            <p>If you want to collaborate with me in the future. Don’t hesitate to shoot me an email!.</p>
          </div>
        </div>
      </div>
    </div>
  )
}
