import './topbar.css';
import { Outlet, Link } from "react-router-dom";
import { NavLink } from 'react-router-dom';
import BadgeIcon from '@mui/icons-material/Badge';
import HomeIcon from '@mui/icons-material/Home';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import IconButton from '@material-ui/core/IconButton'

function Topbar() {
  return (
    <>
    <div className="topnav">
        <div className="container1">
        <div className="logo1">
            <h3>WORT</h3>
        </div>
        <div className="insideContent">
            {/* <NavLink exact activeClassName="active" to='/WortsPage/about'>About</NavLink>
            <NavLink activeClassName="active" to='/WortsPage/'>Home</NavLink> */}
            {/* <NavLink activeClassName="active" to='/WortsPage/cv'>‎ CV ‎ </NavLink> */}
            <Link to="/WortsPage/about"><IconButton><AccountBoxIcon/></IconButton></Link>
            <Link to="/WortsPage/"><IconButton><HomeIcon/></IconButton></Link>
            <Link to="/WortsPage/cv"><IconButton><BadgeIcon/></IconButton></Link>
        </div>
        <div className="login">
            <NavLink to='/WortsPage/support'> Support <img style={{height: "15px", width: "15px", textAlign: "center", display: "inline-flex"}} src="https://cdn-icons-png.flaticon.com/512/1076/1076984.png" alt="" /></NavLink>
        </div>
        </div>
    </div>
      <Outlet/>
    </>
  )
}

export default Topbar;