import "./home.css";
import img from "../props/_DSC6081.JPG";  

function Home() {

  const Facebook = () =>{ 
    let path = `https://www.facebook.com/w0rzt/`; 
    window.location.assign(path);
  }
  
  const Instagram = () =>{ 
    let path = `https://www.instagram.com/wortttttz/`; 
    window.location.assign(path);
  }

  const Steam = () =>{ 
    let path = `https://steamcommunity.com/id/lethanhdat7620031`; 
    window.location.assign(path);
  }

  const Github = () =>{ 
    let path = `https://github.com/Wortzzzzz`; 
    window.location.assign(path);
  }

  return (
    <div id="home" className="container home-container">
      <div className="logo">
      <div className="inner_circle"></div>
        <div className="hover-show active">
          <span onClick={Instagram} className="circle"></span>
          <span onClick={Facebook} className="circle"></span>
          <span onClick={Github} className="circle"></span>
          <span onClick={Steam} className="circle"></span>
        </div>
        <img src={img} alt="" />
      </div>
    </div>
  );
}

export default Home;
